.SnackBarItem {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: var(--space-m) var(--space-2xl) var(--space-m) var(--space-m);
  color: var(--color-typo-primary);
  border-radius: var(--space-xs);
  -webkit-box-shadow: var(--shadow-group);
          box-shadow: var(--shadow-group);
}

  .SnackBarItem_showProgress_line {
      padding-bottom: calc(var(--space-m) + var(--space-2xs));
    }

  .SnackBarItem-ActionButton {
    margin-top: calc(var(--space-2xs) + var(--space-xs));
  }

  .SnackBarItem_status_system {
      background-color: var(--color-bg-system);
    }

  .SnackBarItem_status_normal {
      background-color: var(--color-bg-normal);
    }

  .SnackBarItem_status_success {
      background-color: var(--color-bg-success);
    }

  .SnackBarItem_status_warning {
      background-color: var(--color-bg-warning);
    }

  .SnackBarItem_status_alert {
      background-color: var(--color-bg-alert);
    }

  .SnackBarItem-Timer {
    margin-right: var(--space-m);
  }

  .SnackBarItem-Line {
    position: absolute;
    left: 0%;
    bottom: 0;
  }

  .SnackBarItem-Icon {
    -webkit-box-flex: 0;
        -ms-flex: none;
            flex: none;
    margin-right: var(--space-m);
  }

  .SnackBarItem-Content {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
  }

  .SnackBarItem-CloseButton {
    position: absolute;
    top: var(--space-2xs);
    right: var(--space-2xs);
  }
