.SnackBarLine {
  overflow: hidden;
  width: 100%;
  height: var(--space-m);
  border-radius: var(--space-xs);
  opacity: 0.8;
}

  .SnackBarLine::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 200%;
    height: var(--space-2xs);
    background: -webkit-gradient(
      linear,
      left top, right top,
      from(var(--color-bg-default)),
      color-stop(50%, var(--color-bg-default)),
      color-stop(50%, var(--color-bg-ghost)),
      to(var(--color-bg-ghost))
    );
    background: linear-gradient(
      90deg,
      var(--color-bg-default) 0%,
      var(--color-bg-default) 50%,
      var(--color-bg-ghost) 50%,
      var(--color-bg-ghost) 100%
    );
    -webkit-transition: -webkit-transform 1s linear;
    transition: -webkit-transform 1s linear;
    transition: transform 1s linear;
    transition: transform 1s linear, -webkit-transform 1s linear;
    -webkit-transform: translate(var(--progress-line-progress));
            transform: translate(var(--progress-line-progress));
  }
